import React, { Component } from "react";

export class About extends Component {
  render() {
    return (
      <div className="container my-4">
        <h3>What We Are</h3>
        <h1>Who we are?</h1>
        <p>
          We are a team of technology lovers, web designers, developers, and
          marketers following our passion and working to make the dream projects
          come true. Starting with a dream of being the number 1 agency for
          software and web-based solutions and custom services worldwide, we are
          being one of the best and leading web development agencies across the
          globe.
        </p>
      </div>
    );
  }
}

export default About;